@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@700&display=swap');

body {
  margin: 0;
  padding: 0;
}

.App {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2f74c0;
  font-family: "Neucha", cursive;
}

.heading {
  font-size: 40px;
  margin: 30px 0;
  color: #fff;
  z-index: 1;
  text-align: center;
}

@media screen and (max-width: 800px) {
  .heading {
    margin: 15px 0;
    font-size: 35px;
  }  
}
